<template>
  <div class="refunds">
    <back-header title="申请退款" />
    <scroll clickable>
      <refund-goods :goods="goods"/>
      <div class="form">
        <div class="input" @click="openPicker">
          <label>退款原因</label>
          <span :class="{active: reason}">{{ reason || '请选择' }} <van-icon class="icon-right" name="arrow" /></span>
        </div>
        <div class="input">
          <label>退款金额：<span>¥</span></label>
          <input @blur.prevent="inputBlur" :disabled="status === 0" class="price" type="text" v-model="money">
        </div>
        <div class="input">
          <label>退款说明</label>
          <input @blur.prevent="inputBlur" type="text" v-model="desc" placeholder="选填">
        </div>
      </div>
    </scroll>
    <div class="apply" @click="submit">申请退款</div>
    <van-popup v-model="showPicker" round position="bottom">
      <van-picker
        show-toolbar
        :columns="columns"
        @cancel="showPicker = false"
        @confirm="confirm"
      />
    </van-popup>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import RefundGoods from '../../components/refund-goods/refund-goods'
import { post } from '../../common/js/http'
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'refunds',
  components: {
    Scroll,
    BackHeader,
    RefundGoods
  },
  data() {
    return {
      showPicker: false,
      reason: '',
      columns: [
        '退运费',
        '大小/尺寸与商品描述不符',
        '颜色/图案/款式与商品描述不符',
        '材质面料与商品描述不符',
        '功能/效果与商品描述不符',
        '做工问题',
        '缩水/褪色'
      ],
      goods: {},
      money: '',
      desc: '',
      status: 0
    }
  },
  created() {
    this.goods = this.$route.params.goods
    this.money = this.goods.price
    this.status = this.$route.params.status
    if (this.status === 0) {
      this.reason = '不喜欢/不想要'
    }
  },
  computed: {
    ...mapState(['routeHistory'])
  },
  methods: {
    ...mapMutations(['changeHistory']),
    inputBlur () {
      const u = navigator.userAgent
      const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
      if (isIOS) {
        setTimeout(() => {
          const scrollHeight = document.documentElement.scrollTop || document.body.scrollTop || 0
          window.scrollTo(0, Math.max(scrollHeight - 1, 0))
        }, 200)
      }
    },
    openPicker() {
      if (this.status === 0) return
      this.showPicker = true
    },
    confirm(val) {
      this.reason = val
      this.showPicker = false
    },
    submit() {
      if (!this.reason) {
        this.$toast.fail('请选择理由')
        return
      }
      if (!this.money) {
        this.$toast.fail('请选择金额')
        return
      }
      const loading = this.$toast.loading()
      post('/shop/order/applyReturnGoods', {
        ordergoodsid: this.goods.goodsId,
        reason: this.reason,
        amount: this.money,
        desc: this.desc
      })
        .then(({ data: { data } }) => {
          loading.close()
          this.changeHistory(this.routeHistory.splice(0, this.routeHistory.length - 1))
          this.$router.replace(`/order/${data.id}?type=4`)
        })
        .catch(() => {
          loading.close()
        })
    }
  }
}
</script>

